.hero-container {
  background-image: linear-gradient(-199deg, rgba(212, 146, 4, .98) 4%, #8E0000 92%);
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  &:hover {
    .hero-oval--right {
      top: 0;
      right: 300px;
      transition: 6s ease-out;
    }
    .hero-oval--left {
      top: 0;
      left: 300px;
      transition: 6s ease-out;
    }
  }
}

.hero-ovals {
  z-index: 0;
  position: relative;
}

.hero-oval--right {
  position: absolute;
  top: -900px;
  right: -580px;
}

.hero-oval--left {
  position: absolute;
  padding: 0;
  top: 800px;
  left: -400px;
}

.site-header {
  width: 100%;
  height: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 5px;
  z-index: 1;
  position: relative;
  @include M {
    height: 85px;
    justify-content: flex-start;
    align-items: flex-start;
  }
  &__logos {
    cursor: pointer;
    padding: 15px 0 0 30px;
    @include XS {
      padding: 10px 0 0 10px;
    }
  }
  &__logo {
    @include XL {
      width: 178px;
      height: auto;
    }
    @include L {
      width: 119px;
      height: auto;
    }
    @include XS {
      width: 90px;
      height: auto;
    }
  }
  &__logo--scrolled {
    display: none;
    @include XL {
      width: 178px;
      height: auto;
    }
    @include L {
      width: 119px;
      height: auto;
    }
    @include XS {
      width: 90px;
      height: auto;
    }
  }
  &__navs {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    color: #fff;
  }
}

.nav1-form {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $orange;
  opacity: .95;
  width: 0;
  height: 0;
  top: -200px;
  visibility: hidden;
  transition: 0.7s;
  &--active {
    visibility: visible;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 8;
  }
  &__input {
    background: none;
    border: none;
    border-bottom: 2px solid #efa221;
    width: 50vw;
    font-size: 51px;
    color: rgba(255, 255, 255, 0.7);
    outline: none;
    font-weight: 400;
    &::placeholder {
      color: rgba(255, 255, 255, 0.7)
    }
    @include M {
      font-size: 40px;
    }
    @include S {
      font-size: 35px;
    }
    @include XS {
      font-size: 30px;
    }
  }
}

.nav1-form-icon {
  position: absolute;
  width: 35px;
  height: 40px;
  opacity: 0.8;
  z-index: 9;
  right: 30px;
  top: 20px;
  display: block;
  cursor: pointer;
  &__line {
    height: 4px;
    display: block;
    margin-bottom: 9px;
    background-color: #fff;
    transition: 0.7s ease-out;
    &:first-child {
      @extend .nav1-form-icon__line;
      transform-origin: top left;
      transform: rotate(45deg) scaleX(1.13);
    }
    &:nth-child(2) {
      @extend .nav1-form-icon__line;
      opacity: 0;
    }
    &:last-child {
      @extend .nav1-form-icon__line;
      transform-origin: bottom left;
      transform: rotate(-45deg) scaleX(1.13);
    }
  }
}
.nav1-list {
  font-weight: 300;
  display: flex;
  align-items: center;
  margin-top: 10px;
  @include M {
    flex-direction: column;
    align-items: flex-start;
  }
  &__arrow {
    background: url(../images/down-arrow-white.svg);
    background-position: 0;
    background-repeat: no-repeat;
    width: 15px;
    height: 15px;
    margin-right: 30px;
    cursor: pointer;
    transition: 1s;
    &--active {
      transform: rotate(180deg);
      transition: 1s;
    }
    @include M {
      position: absolute;
      top: 18px;
      right: 31px;
    }
    @include XS {
      top: 60px;
    }
  }
  &__item {
    display: inline-block;
    cursor: pointer;
    padding-right: 40px;
    padding-left: 15px;
    border-right: 1px solid #fff;
    transition: all 0.3s ease;
    @include M {
      padding: 0 15px;
      margin-left: 5px;
      border-right: none;
    }
    &--search {
      @extend .nav1-list__item;
      background: url(../images/magnifying-glass.png);
      background-position: 0;
      background-repeat: no-repeat;
      background-size: 14px;
      padding-left: 30px;
      @include M {
        padding: 0 15px;
      }
    }
    &--flag {
      margin-left: 15px;
      padding-right: 10px;
      cursor: pointer;
      @include M {
        margin-left: 5px;
        position: absolute;
        right: 70px;
        top: 9px;
      }
      @include XS {
        top: 50px;
        right: 0;
      }
    }
  }
}

.nav1-list-contact {
  background-color: $orange;
  padding: 12px 70px 12px 15px;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s ease;
  @include M {
    padding: 0 15px;
    background: none;
    margin-left: 5px;
  }
  &__text {
  }
  &__image {
    padding-left: 10px;
    @include M {
      display: none;
    }
  }
}

.language {
  overflow: hidden;
  position: relative;
  padding: 5px 10px;
  height: 32px;
  top: 0;
  transition: 1s;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  border-radius: 5px;
  background-image: linear-gradient(to bottom right, #fff, #ccc);
  &--active {
    height: 100px;
    top: 34px;
    transition: 1s;
    @include M {
      top: 0;
    }
  }
  &--inactive {

  }
}

.nav2 {

}


.nav2-list {
  &--hidden-on-small-screen {
    padding-right: 70px;
    padding-top: 26px;
    letter-spacing: .5px;
    display: flex;
    @include XL {

    }
    @include L {
      padding-right: 50px;
    }
    @include M {
      display: none;
    }
  }
  &--visible-on-small-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    background-color: $orange;
    opacity: .95;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    padding-top: 30px;
    z-index:6;
  }
  &__item {
    cursor: pointer;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    text-align: center;
    font-size: 16px;
    padding: 5px;
    margin-left: 16px;
    transition: all 0.3s ease;
    @include XL {
      font-size: 15px;
    }
    @include L {
      font-size: 14px;
    }
    @include M {
      font-size: 36px;
      margin: 0 3px 10px 3px;
      padding: 15px 5px;
    }
    @include S {
      font-size: 28px;
    }
    @include XS {
      font-size: 24px;
      margin: 0 3px 8px 3px;
      padding: 10px 3px;
    }
  }
}

.nav2-icon {
  position: absolute;
  width: 35px;
  height: 40px;
  opacity: 0.8;
  right: 10px;
  top: 10px;
  z-index: 7;
  display: none;
  cursor: pointer;
  @include M {
    display: block;
  }
  @include XS {
    right: 15px;
  }
  &:hover {
    opacity: 0.6;
  }
  &--active {
    .nav2-icon__line--first {
      transform: rotate(45deg) scaleX(1.13);
    }
    .nav2-icon__line--second {
      opacity: 0;
    }
    .nav2-icon__line--third {
      transform: rotate(-45deg) scaleX(1.13);
    }
  }
  &__line {
    height: 4px;
    display: block;
    margin-bottom: 9px;
    background-color: #fff;
    transition: 0.7s ease-out;
    &--first {
      @extend .nav2-icon__line;
      transform-origin: top left;
    }
    &--second {
      @extend .nav2-icon__line;
    }
    &--third {
      @extend .nav2-icon__line;
      transform-origin: bottom left;
    }
  }
}



.hero {
  max-width: 1566px;
  margin: auto;
  padding: 0 5px;
  &__heading {
    max-width: 1100px;
    padding-top: 100px;
    padding-bottom: 68px;
    font-weight: 800;
    font-size: 80px;
    color: #FFF;
    letter-spacing: .5px;
    line-height: 80px;
    text-transform: uppercase;
    @include XL {
      font-size: 60px;
      line-height: 60px;
    }
    @include L {
      font-size: 40px;
      line-height: 40px;
      padding-bottom: 40px;
    }
    @include M {
      font-size: 30px;
      line-height: 30px;
    }
    @include S {
      padding-top: 60px;
      font-size: 26px;
      line-height: 26px;
    }
    @include XS {
      font-size: 22px;
      line-height: 22px;
    }
  }
  &__subheading {
    max-width: 791px;
    padding-bottom: 54px;
    font-weight: 400;
    font-size: 22px;
    color: #FFF;
    line-height: 32px;
    @include L {
      font-size: 20px;
      line-height: 20px;
    }
    @include M {
      font-size: 18px;
      line-height: 18px;
    }
    @include S {
      font-size: 16px;
      line-height: 16px;
    }
    @include XS {
      font-size: 14px;
      line-height: 14px;
    }
  }
  &__link {
    cursor: pointer;
    text-decoration: none;
    display: inline-block;
    padding: 4px;
    font-size: 24px;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #fff;
    font-weight: 700;
    border-bottom: 3px solid #fff;
    transition: all 0.3s ease;
    @include L {
      font-size: 20px;
    }
    @include M {
      font-size: 16px;
    }
    &:hover {
      background: #fff;
      color: $orange;
    }
  }
}

.triangle-button {
  display: inline-block;
  width: 20vw;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  margin: 0 auto;
  &__image {
    opacity: 0.8;
    &:hover {
      opacity: 1;
      transform: scale(1.4);
      transition-duration: 0.3s;
    }
  }
}

.main-nav-scrolled {
  position: fixed;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 0 8px rgba(0,0,0,.3);
  margin-top: 0;
  top: -130px;
  z-index: 8;
  .nav1-list__item {
    color: $orange;
    &:hover {
      color: #fff;
    }
  }
  .nav1-list__arrow {
    background: url(../images/down-arrow-black.svg);
    background-position: 0;
    background-repeat: no-repeat;
    width: 15px;
    height: 15px;
    margin-right: 30px;
    cursor: pointer;
    transition: 1s;
  }
  .nav1-list-contact__text {
    @include M {
      color: $orange;
    }
  }

  .nav2-list--hidden-on-small-screen {
    color: $orange;
  }

  .nav2-icon--active {
    .nav2-icon__line--first {
      background-color: #fff;
    }
    .nav2-icon__line--third {
      background-color: #fff;
    }
  }

  .nav2-icon__line {
    background-color: $orange;
  }
  .site-header__logo {
    display:none;
  }
  .site-header__logo--scrolled {
    display: inline-block;
  }
}

.site-header-up {
  top: -150px;
  transition: 0.7s;
}

.site-header-down {
  top: 0!important;
  transition: 0.7s;
}
